.nav-links {
	margin-right: 20px;
	border-radius: 10px;
	height: 36px;
	background-image: linear-gradient(135deg, #ebd15f, #fa0);
	color: green; /*will change color of text within the element */
	text-align: center;
}

.nav-links:hover {
	box-shadow: 0 0 18px rgba(255, 170, 0, 0.788);
	color: white; /*will change color of text within the element on hover */
}

#nav-drop {
