	background-color: #f5f5f5;
	color: #4a4a4a;
	font-size: .875em;
	overflow-x: auto;
	padding: 1.25rem 1.5rem;
	white-space: pre;
	word-wrap: normal;
}

pre code {
	background-color: transparent;
	color: currentColor;
	font-size: 1em;
	padding: 0;
}

table td,
table th {
	vertical-align: top;
}

table td:not([align]),
table th:not([align]) {
