.card {
	box-shadow: 0 0 15px rgba(196, 133, 8, 0.788);
	outline: solid 2px rgb(231, 158, 10);
	padding: 10px;
	border-radius: 10px;
	width: 250px;
	height: 400px;
	margin-left: 30px;
	margin-right: 10px;
	margin-top: 30px;
	padding-top: 20px;
}
.card:hover {
	box-shadow: 0 0 20px rgba(211, 209, 206, 0.788);
	transform: scale(1.05, 1.05); /* Equal to scaleX(2) scaleY(0.5) */
}
.title {
	color: rgb(231, 158, 10);
	border-bottom: solid 1px rgba(211, 209, 206, 0.788);
}
.subtitle {
	color: cadetblue;
}
.soft-hard-cap {
	color: bisque;
	margin-top: 10px;
}
.card-btn {
	background-image: linear-gradient(135deg, #ebd15f, #fa0);
	width: 70px;
	height: 20px;
	border-radius: 5px;
	color: rgb(10, 10, 10);
}

.card-btn:hover {
	box-shadow: 0 0 10px rgba(255, 170, 0, 0.788);
}

.containers-card {
	align-items: center;
	justify-content: center;
}

.card-whitelist-text {
	margin-right: 30px;
	font-size: 18px;
}
.info-panel-title {
	font-size: 30px;
	color: rgb(189, 128, 8);
}
